import React from "react";
import * as headerStyles from "./header.module.css"
import {Navbar,Nav} from 'react-bootstrap'
import * as Constants from "../constants/contants"
import { StaticImage } from "gatsby-plugin-image"

export default class Header extends React.Component{
    constructor(props) {
        super(props);
        this.state = {activeKey: Constants.home}
    }

    selectTab (sTab){
        if (sTab == Constants.home){            
            gtag('event', 'home');            
            this.setState({activeKey: Constants.home});
        }else{
            if (sTab == Constants.portfolio){
                gtag('event', 'portfolio'); 
                this.setState({activeKey: Constants.portfolio});
            }else{
                if (sTab == Constants.cv){
                    gtag('event', 'cv'); 
                    this.setState({activeKey: Constants.cv});
                }else{
                    gtag('event', 'contact'); 
                    this.setState({activeKey: Constants.contact});
                }            
            }
        }
        this.props.changeComponent(sTab);
    }
    render(){
        return (
            <header className={headerStyles.header}>
                <Navbar id="menu-nav" collapseOnSelect  expand="lg" className={headerStyles.navbar}>
                    <div id="h-block" className={headerStyles.logo_block1} onClick={() => {gtag('event', 'logo_aws_cert');  window.open('https://www.credly.com/badges/99d3da1d-e689-47a3-910a-e7c012f893b4/public_url', '_blank')}}>
                        <StaticImage src="../images/logo8.png" alt="Logo Icon" layout="constrained" height={57}/>
                        <StaticImage src="../images/aws-certified-logo_color-horz_180x30.png" alt="AWS Icon" layout="fixed" height={21} width={132} style={{marginTop:'-16px',display:'block'}}/>                                                
                    </div>
                    <div className={headerStyles.logo_block2}>
                        <div style={{height:'100%'}}>
                            <Navbar.Toggle className={headerStyles.tooglebtn} aria-controls="responsive-navbar-nav" />
                            <Navbar.Collapse id="responsive-navbar-nav" className={headerStyles.responsive_navbar}>
                                <Nav className="mr-auto" activeKey={this.state.activeKey} onSelect={(selectedKey) => this.selectTab(selectedKey)}>
                                    
                                        <Nav.Link className={headerStyles.navbar_link} eventKey={Constants.home} href="#" onSelect={() => this.selectTab(Constants.home)}>Home</Nav.Link>
                                    
                                    
                                        <Nav.Link className={headerStyles.navbar_link} eventKey={Constants.portfolio} href="#" onSelect={() => this.selectTab(Constants.portfolio)} data-description="Examples of Technology Used">Portfolio</Nav.Link>
                                    
                                        <Nav.Link className={headerStyles.navbar_link}  eventKey={Constants.cv} href="#" onSelect={() => this.selectTab(Constants.cv)} data-description="My resume">CV</Nav.Link>
                                    
                                        <Nav.Link className={headerStyles.navbar_link}  eventKey={Constants.contact} href="#" onSelect={() => this.selectTab(Constants.contact)} data-description="Inquire within">Contact</Nav.Link>
                                    
                                </Nav>
                            </Navbar.Collapse>
                        
                        </div>
                    </div>		
                </Navbar>
            </header>
        );
    }
}